  &--purple {
    color: $purple-btn;
    &::after {
      background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3C!-- Generator: Adobe Illustrator 22.0.1, SVG Export Plug-In . SVG Version: 6.00 Build 0) --%3E%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 26.5 43.8' style='enable-background:new 0 0 26.5 43.8;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0%7Bfill:%239e1679;%7D%0A%3C/style%3E%3Crect x='-1.7' y='9.5' transform='matrix(0.7071 0.7071 -0.7071 0.7071 13.2576 -5.4915)' class='st0' width='30' height='7.5'/%3E%3Crect x='-1.7' y='26.8' transform='matrix(0.7071 -0.7071 0.7071 0.7071 -17.6885 18.3097)' class='st0' width='30' height='7.5'/%3E%3C/svg%3E%0A");
